}

[class*=customify-grid-][class*="-equalHeight"]>[class*=customify-col-]>*,
[class*=customify-grid-][class*="-equalHeight"]>[class*=customify-col_]>*,
[class*=customify-grid-][class*="-equalHeight"]>[class~=customify-col]>* {
