}

[class*=customify-grid-][class*="-noBottom"]>[class*=customify-col-],
[class*=customify-grid-][class*="-noBottom"]>[class*=customify-col_],
[class*=customify-grid-][class*="-noBottom"]>[class~=customify-col] {
