    line-height       : 1em;
    font-size         : 1em;
    position          : relative;
    padding           : 0;
    border-radius     : 50%;
    box-shadow        : 0 2px 2px 0 rgba(0, 0, 0, .16), 0 0 0 1px rgba(0, 0, 0, .08);
    background-color  : rgba(255, 255, 255, .5);
